.rui-search-form {
  background: #ffffff;
  box-shadow: 0 2px 16px 0 rgba(69, 165, 255, 0.12);
  border-radius: 8px;
  filter: blur(0px);
  padding: 12px 0 12px 20px;
  margin-bottom: 12px;
  user-select: none;
}
.rui-search-form-btn {
  display: flex;
  justify-content: flex-end;
  grid-row-start: 1;
  grid-column-end: -1;
  text-align: right;
  padding-right: 20px;
}
.rui-search-form-more {
  display: flex;
  align-items: center;
  font-size: 14px;
  color: #1890FF;
  margin-right: 12px;
  cursor: pointer;
  user-select: none;
}
.rui-search-form-icon {
  transition: all 0.3s;
  font-size: 20px;
  margin-left: 4px;
}
.rui-search-form-up {
  transform: rotate(-180deg);
}
.rui-search-form-search {
  margin-right: 12px;
}
.rui-search-form-grid-container {
  display: grid;
  transition: all 0.3s;
  overflow: hidden;
}
.rui-search-form-grid-item {
  display: flex;
  align-items: center;
  padding-right: 20px;
}
.rui-search-form-grid-item-left {
  flex-shrink: 0;
  font-size: 14px;
  font-weight: 400;
  color: #35383d;
  width: 100px;
  text-align: justify;
  text-align-last: justify;
}
.rui-search-form-grid-item-left:after {
  content: '';
  width: 100%;
  height: 0;
}
.rui-search-form-grid-item-right {
  height: 100%;
  flex: 1;
}
.rui-search-form-grid-item-right .ant-calendar-picker {
  width: 100% !important;
}
.rui-search-form-group-item {
  display: flex;
  align-items: center;
  padding-right: 0;
  height: 100%;
}
.rui-search-form-group-item .ant-select-selection {
  border-radius: 4px 0 0 4px;
}
.rui-search-form-group-item-right {
  flex: 1;
  height: 100%;
}
.rui-search-form-group-item-right .ant-select-selection {
  border-radius: 0 4px 4px 0;
  border-left: 0;
}
.rui-search-form-group-item-right .ant-input {
  border-radius: 0 4px 4px 0;
  border-left: 0;
}
.rui-search-form-slide-item-container {
  top: 7px;
}
.rui-search-form-search-result {
  display: flex;
  flex-wrap: wrap;
  margin: 12px 0 0 0;
  border-top: 1px dashed #e4eaf0;
  overflow: hidden;
}
.rui-search-form-label-container {
  margin: 12px 2px 0 0;
}
.rui-search-form-label {
  margin-right: 10px;
  color: #666666;
  height: 24px;
  line-height: 22px;
  background: #FAFAFA;
  border: 1px solid #e8e8e8;
  padding: 0 8px;
  border-radius: 4px;
  cursor: pointer;
}
.rui-search-form-label:hover {
  background-color: #1890FF;
  color: white;
  border-color: #1890FF;
}
.rui-search-form-close {
  margin-left: 4px;
  font-size: 12px;
}
